import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-radio-configedit',
  templateUrl: './radio-configedit.component.html',
  styleUrls: ['./radio-configedit.component.css']
})
export class RadioConfigeditComponent implements OnInit {
	data: any = {};
	isSuccessAlert: any = false;
  isDangerAlert: any = false;
  constructor(
  	public api: ApiService,
  	public router: Router
  	) {
  	this.data = this.router.getCurrentNavigation().extras.state.data;
  	 }

  ngOnInit(): void {
  }

  saveProgram(){
  	this.api.provideData({
  		type: 'editradioconfigadm',
  		config_value: this.data.config_value,
  		config_id: this.data.config_id
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.isSuccessAlert = true;
  		setTimeout(()=>{ 
		    this.moveTo('radio-config');
		 }, 2000);
  	});
  }

  moveTo(url){
  	this.router.navigate(['/'+url]);
  }
}
