import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RadioConfigComponent } from './radio-config.component';
import { RadioConfigeditComponent } from './radio-configedit.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Pengaturan Radio City Guide 911 FM'
    },
    children: [
      {
        path: '',
        component: RadioConfigComponent,
      },
      {
        path: 'radio-configedit',
        component: RadioConfigeditComponent,
        data: {
          title: 'Ubah Pengaturan Radio'
        }
      },
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RadioConfigRoutingModule { }
